import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/preloader";
import yesIcon from "../../../assets/images/checkMarkSignjpg.jpg";
import noIcon from "../../../assets/images/XMarkSign.png";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = (props) => {

    if(!props.profile){
        return <Preloader/>
    }

    return (
        <div>
            {/*<div><img*/}
            {/*    src='https://images.ctfassets.net/hrltx12pl8hq/7yQR5uJhwEkRfjwMFJ7bUK/dc52a0913e8ff8b5c276177890eb0129/offset_comp_772626-opt.jpg?fit=fill&w=800&h=300'></img>*/}
            {/*</div>*/}
            <div className={s.descriptionBlock}>
                <img src={props.profile.photos.large} style={{width:"100px", borderRadius:"50%"}}/>

                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>

                <h2>About Me</h2>
                <h3>{props.profile.fullName}</h3>
                <div>{props.profile.aboutMe}</div>
                <h2>My contacts</h2>
                <ul>
                    <li>Facebook: <a href={props.profile.contacts.facebook}>{props.profile.contacts.facebook}</a></li>
                    <li>Twitter:  <a href={props.profile.contacts.twitter}>{props.profile.contacts.twitter}</a></li>
                    <li>Instagram:  <a href={props.profile.contacts.instagram}>{props.profile.contacts.instagram}</a></li>
                    <li>Youtube:  <a href={props.profile.contacts.youtube}>{props.profile.contacts.youtube}</a></li>
                    <li>Github:  <a href={props.profile.contacts.github}>{props.profile.contacts.github}</a></li>
                </ul>
                <div>Looking for a job:
                    <img src={props.profile.lookingForAJob ? yesIcon : noIcon} style={{width:"50px"}}/>
            </div>
            </div>
        </div>
    )
}

export default ProfileInfo;