import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
    return (<div className={s.item}>
            <img
                src='data:image/png;base64,'></img>
            {props.message}
            <div>
                <span>{props.likesCount} likes</span>
            </div>
        </div>

    )
}

export default Post;